import { css } from "@emotion/react";
import { AnchorHTMLAttributes, PropsWithChildren, Ref } from "react";
import ReactMarkdown from "react-markdown";
import remarkGfm from "remark-gfm";
import User from "../review/User";
import { findAndReplace } from "mdast-util-find-and-replace";
import { Blockquote, Code, List } from "@mantine/core";
import { visit } from "unist-util-visit";
import remarkGemoji from "remark-gemoji";
import rehypeGithubAlert from "rehype-github-alert";
import rehypeGithubDir from "rehype-github-dir";
import rehypeGithubEmoji from "rehype-github-emoji";
import rehypeGithubHeading from "rehype-github-heading";
import rehypeGithubImage from "rehype-github-image";
import rehypeGithubLink from "rehype-github-link";
import rehypeRaw from "rehype-raw";
import rehypeSanitize from "rehype-sanitize";
import rehypeStringify from "rehype-stringify";
import remarkGithubYamlMetadata from "remark-github-yaml-metadata";
import remarkParse from "remark-parse";
import classes from './MarkdownRenderer.module.css';


export const markdownCss = css({
  "p, li": {
    fontSize: "var(--mantine-font-size-md)",
  },
  "ul, ol": {
    paddingInlineStart: "var(--mantine-spacing-lg)",
  },
  "h1, h2, h3, h4, h5, h6": {
    fontSize: "var(--mantine-font-size-lg)",
  },
});
const userGroup = "[\\da-z][-\\da-z_]{0,38}";
const mentionRegex = new RegExp("(?:^|\\s)@(" + userGroup + ")", "gi");

function remarkMentions(
  opts = { usernameLink: (username: string) => `/${username}` }
) {
  function replaceMention(value: string, username: string) {
    /** @type {PhrasingContent[]} */
    let whitespace = [];

    // Separate leading white space
    if (value.indexOf("@") > 0) {
      whitespace.push({
        type: "text",
        value: value.substring(0, value.indexOf("@")),
      });
    }

    return [
      ...whitespace,
      {
        type: "link",
        url: opts.usernameLink(username),
        children: [{ type: "text", value: value.trim() }],
      },
    ];
  }
  // @ts-ignore
  return (tree, _file) => {
    findAndReplace(tree, [[mentionRegex, replaceMention]]);
  };
}

const commentRegex = /(?:\<!--).*(?:-->)/g;

function remarkComments() {
  // @ts-ignore
  return (tree) => {
    visit(tree, "html", (node, index, parent) => {
      if (node.value?.startsWith("<!--") && node.value?.endsWith("-->")) {
        // Remove the comment node from the parent
        if (parent && typeof index === "number") {
          parent.children.splice(index, 1);
        }
      }
    });
  };
}

function LinkRenderer(props: AnchorHTMLAttributes<HTMLAnchorElement>) {
  const { children } = props;
  if (typeof children === "string" && children.startsWith("@")) {
    return <User login={children.slice(1)} prefix="@" />;
  }
  return <a {...props} />;
}

export default function MarkdownRenderer({
  children,
  ref = undefined,
}: PropsWithChildren & {
  children: string | null;
  ref?: Ref<HTMLDivElement>;
}) {
  return (
    <div ref={ref} className={classes.markdown}>
      <ReactMarkdown
        remarkPlugins={[
          remarkGfm,
          remarkMentions,
          remarkComments,
          remarkGemoji,
          remarkGithubYamlMetadata,
          remarkParse,
        ]}
        rehypePlugins={[
          rehypeGithubAlert,
          rehypeGithubDir,
          rehypeGithubEmoji,
          rehypeGithubHeading,
          rehypeGithubImage,
          rehypeGithubLink,
          rehypeRaw,
          rehypeSanitize,
          rehypeStringify,
        ]}
        components={{
          a: LinkRenderer,
          // @ts-ignore
          blockquote: Blockquote,
          // @ts-ignore
          code: Code,
          // @ts-ignore
          ul: List,
        }}
      >
        {children}
      </ReactMarkdown>
    </div>
  );
}
